$foreground: #444;
$background: #fff;

* {
  margin: 0;
  padding: 0;
}

.background-canvas {
  position: absolute;
  top: 0;
  left: 0;

  z-index: -1;
}

.container {
  background: $background;
  width: 60%;
  max-width: 400px;

  margin: 20vh auto;

  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

  .title {
    margin-bottom: 10px;

    text-align: center;

    font-size: 5em;
    color: $foreground;
  }

  .subtitle {
    margin-bottom: 30px;

    text-align: center;

    font-size: 2em;
    color: $foreground;
  }

  .nav {
    .links {
      list-style: none;

      display: flex;

      .link {
        flex: 1;
        text-align: center;
        position: relative;

        margin: 0 10px;
        padding: 10px 0;

        cursor: pointer;

        a {
          font-size: 1em;
          color: $foreground;
          text-decoration: none;

          &::before,
          &::after {
            display: block;
            content: '';

            position: absolute;
            left: 0;
            top: 0;

            width: 100%;
            height: 100%;

            opacity: 0;

            transition: all 0.2s ease-out;
          }

          &::before {
            border-top: 1px solid $foreground;
            transform: translateY(50%);
          }
          &::after {
            border-bottom: 1px solid $foreground;
            transform: translateY(-50%);
          }

          &:hover {
            &::before, &::after {
              transform: translateY(0%);
              opacity: 1;
            }
          }
        }
      }
    }
  }
}
